.vai404Description1 {
  font-weight: 100;
  font-size: 25px;
}

.vai404Description2 {
  font-weight: 100;
  font-size: 16px;
}

.vai404Cloud {
  margin-top: 50px;
  height: 132.84px;
  width: 201px;
}

.vai404Button {
  background-color: #13ad83;
  width: 82px;
  height: 36px;
  padding: 10px 24px;
  font-weight: 400;
  font-size: 12px;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  margin-top: 70px;
}

@media (min-width: 515px) {
  .vai404Page {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .vai404Cloud {
    margin-top: 50px;
    height: 207.91px;
    width: 314.58px;
  }

  .vai404Description1 {
    font-size: 30px;
  }

  .vai404Description2 {
    font-size: 24px;
  }
}

@media (max-width: 514px) {
  .vai404Page {
    margin-top: 60px;
  }
}
