.vaiTbodyDiv {
  height: 538.56px;
}

.vaiTableDiv {
  overflow-x: auto;
}

tbody {
  display: block;
}
thead,
tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed; /* even columns width , fix width of table too*/
}

.vaiThClient,
.vaiThTitle,
.vaiThWorker,
.vaiThStarted,
.vaiThEnded,
.vaiThDuration,
.vaiThStatus {
  height: 44.88px;
  background: #13ad83;
  min-width: 164.14px;
  color: #fafafa;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  text-transform: capitalize;
  text-indent: 28px;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  margin: 0px 0px;
  font-weight: 300;
}

.vaiThClient {
  border-radius: 5px 0px 0px 0px;
}

.vaiThStatus {
  border-radius: 0px 5px 0px 0px;
}

tr {
  border-bottom: solid 1px #e2e8f0 !important;
}

.vaiActiveRow > td {
  background-color: #116c53;
  color: #fafafa;
  border: none;
}

td {
  width: 164.14px;
  height: 44.88px;
  background: #ffffff;
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  margin: 0px 0px;
  text-indent: 28px;
  overflow-x: hidden;
  white-space: nowrap;
  font-size: 13px;
  line-height: 16px;
  text-transform: capitalize;
  color: #656565;
}

.vaiRunningStatus,
.vaiSuccessStatus,
.vaiStoppedStatus,
.vaiFailedStatus,
.vaiQueuedStatus {
  box-sizing: border-box;
  backdrop-filter: blur(10px);
  border-radius: 2px;
  font-size: 12px;
  line-height: 14px;
  text-transform: capitalize;
  color: #6d6d73;
  width: 81px;
  height: 18px;
  text-indent: 0px;
  margin-left: 28px;
  text-align: center;
  padding-top: 1.5px;
  border: 1px solid transparent;
}

.vaiRunningStatus {
  background-color: #bdd7ee;
  color: #297fcb;
}

.vaiSuccessStatus {
  background-color: #88d6c1;
  color: #127056;
}

.vaiStoppedStatus {
  background-color: #bfbfbf;
  color: #5c5c5c;
}

.vaiFailedStatus {
  background-color: #f4b183;
  color: #c0550b;
}

.vaiQueuedStatus {
  background-color: #ffd966;
  color: #a48c43;
}

.vaiActiveRow > td > div {
  border-color: #fafafa !important;
  color: #fafafa;
  background-color: transparent !important;
}

.vaiTableFooter {
  background: #ffffff;
  border-radius: 0px 0px 5px 5px;
  width: 100%;
  height: 64px;
  padding: 17px 20px;
}

.vaiListButton {
  background: #13ad83;
  border-radius: 4px;
  padding: 5px 11px;
  color: #fafafa;
  font-size: 12px;
  line-height: 14px;
  border: none;
  margin-right: 10px;
}

.vaiLabel {
  font-size: 12px;
  line-height: 14px;
  color: #656565;
  margin-right: 10px;
}

.vaiPaginationBtn {
  border: 1px solid #e2e8f0;
  width: 26px;
  height: 26px;
  background: #ffffff;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #656565;
}

.vaiPaginationBtn:first-child {
  border-radius: 4px 0px 0px 4px;
}

.vaiPaginationActiveBtn {
  background-color: #13ad83;
  border: none;
  color: #fafafa;
}

.vaiPaginationBtn:last-child {
  border-radius: 0px 4px 4px 0px;
}

.vaiChevLeftIcon,
.vaiChevRightIcon {
  width: 4.38px;
  height: 7.02px;
  margin-top: -1px;
}

.vaiChevLeftIcon {
  transform: rotate(180deg);
}

.vaiFilterIcon {
  margin-bottom: 2px;
}

.vaiListSelection {
  appearance: none;
  background: #13ad83;
  border-radius: 4px;
  padding: 5px 11px;
  color: #fafafa;
  font-size: 12px;
  line-height: 14px;
  border: none;
  margin-right: 10px;
  outline: none;
  cursor: pointer;
}

.vaiListSelection option {
  color: #000000;
  background-color: #ffffff;
}

@media (min-height: 900px) {
  td {
    height: 56.08px !important;
  }

  .vaiTbodyDiv {
    height: 672.96px !important;
  }
}

@media (max-width: 1440px) {
  .vaiThTitle,
  .vaiThWorker,
  .vaiThStarted,
  .vaiThEnded,
  .vaiTdTitle,
  .vaiTdWorker,
  .vaiTdStarted,
  .vaiTdEnded {
    width: 202px !important;
  }
}

@media (max-width: 1980px) {
  .vaiThClient,
  .vaiTdClient {
    width: 106px;
  }

  .vaiThTitle,
  .vaiThWorker,
  .vaiThStarted,
  .vaiThEnded,
  .vaiTdTitle,
  .vaiTdWorker,
  .vaiTdStarted,
  .vaiTdEnded {
    width: 262.25px;
  }

  .vaiThDuration,
  .vaiTdDuration {
    width: 98.44px;
  }

  .vaiThStatus,
  .vaiTdStatus {
    width: 122px;
  }
}
