.vaiLoginBody {
  height: 100vh;
  width: 100vw;
  min-height: 700px;
  position: relative;
  font-family: 'SF';
  overflow: hidden;
}

.vaiLoginPanel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -10px;
  height: 780px;
  width: 520px;
  background-color: #fafafa;
  border-radius: 10px;
}

.vaiLoginLogo {
  height: 60px;
  width: auto;
}

.vaiErrorAlertBox {
  background-color: #df4759;
  width: 390px;
  margin-left: 53px;
  height: 40px;
  border-radius: 4px;
  font-weight: 100;
}

.vaiErrorAlertText {
  font-weight: 400;
  font-size: 12px;
  color: #ff7878;
  margin-top: 5px;
}

.vaiTitleText {
  color: #6d6d73;
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
}

.vaiDescription {
  font-weight: 400;
  font-size: 15px;
  line-height: 17.9px;
  letter-spacing: 3%;
  width: 390px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 40px;
}

.vaiLoginEmailInput,
.vaiLoginPasswordInput {
  padding: 8px 15px 8px 5px;
  width: 390px;
  height: 33px;
  background: #fafafa;
  box-shadow: inset 0px -1px 0px #6d6d73;
  border: none;
  color: #6d6d73;
  font-size: 14px;
  line-height: 16.71px;
  font-weight: 400;
  outline: none;
}

.vaiLoginEmailInput {
  margin-top: 30px;
}

.vaiLoginPasswordInput {
  margin-top: 20px;
  margin-left: 50px;
}

.vaiLoginPasswordInputIcon {
  position: absolute;
  margin-top: 25px;
  right: 0;
  margin-right: 70px;
}

.vaiSignInButton,
.vaiResetPasswordButton {
  background-color: #13ad83;
  width: 390px;
  height: 43px;
  border-radius: 4px;
  padding: 11px 24px;
  border: none;
  color: #ffffff;
  font-size: 18px;
  font-weight: 100;
  line-height: 21px;
  margin-top: 40px;
}

.vaiSignInButtonIcon {
  margin-left: 6px;
  margin-top: -2px;
}

.vaiForgotPasswordText,
.vaiBackText {
  color: #6d6d73;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.03em;
  margin-top: 15px;
}

@media (min-width: 515px) {
  .vaiLoginPanel {
    height: 530px;
  }
}

@media (max-width: 515px) {
  .vaiLoginPanel {
    padding-top: 50px;
  }
}
